



















































































import main from '@/App.vue';
import globaldata from '@/components/global data/global data.vue';
import {swipestr,file,emptyqueue} from '@/components/struct.vue';

function getlist():Array<swipestr>{//更新目录表
	var list:Array<swipestr> = new Array<swipestr>(0);
	globaldata.data.fileempty = new emptyqueue(10);
	uni.request({
		url: 'http://www.cloudcd.top:8080/getfile',
		data: {
			request:"get",
		    src: "/home/ide/cloudann/data/"+main.data.src
		},
		header: {
			'custom-type': 'application/json'
		},
		success: (res) => {
			console.log("s");
			var len = 0;
			var s:string = res.data;
			var l:Array<string> = s.split("\n");
			//console.log(globaldata.data.emptyqueue.pos);
			for(var i:number=0;i<l.length-1;i++){
				list.push(new swipestr(l[i],globaldata.data.fileempty.dequeue(),l[i].indexOf(".cpp")>-1?true:false));
			}
		}
	})
	return list;
};

export default {
			onReady(){
				//globaldata.data.list = new list();
			},
			onShow(){
				//this.list= globaldata.data.list.print();
			},
			data() {
				return {
					show:false,
					addtype:false,
					addname:'',
					btnadd:"add",
					list: getlist(),
					disabled:false,
					options: [
						{
							text: '删除',
							style: {
								backgroundColor: '#dd524d'
							}
						}
					],
					
					sheet_list: [ {
						text: '新建文件夹'
					}, {
						text: '新建文件' 
					}],
					sheet_show:false,
					
					modal_show:false
					
				};
			},
			methods: {
				linkto(isfile) {
					
					if(isfile){
						uni.switchTab({
						    url: '/pages/edit/edit'
						});
					
					}
				},
				modal_cancel(){
					this.addname = "";
				},
				modal_confirm(){
					//addtype为真是文件夹
					this.addname = this.addtype?this.addname+".cpp":this.addname;
					var len = globaldata.data.list.push(this.addname,this.addtype);
					this.list.push(new swipestr(this.addname,len,this.addtype));
					uni.request({
						url: 'http://www.cloudcd.top:8080/getfile',
						data: {
							request:"add",
							user:main.data.user,
						    src: main.data.src,
							name:this.addname,
							isfile:this.addtype
						},
						header: {
							'custom-type': 'application/json'
						},
						success: (res) => {
							console.log(res.data);
						}
					})
				},
				sheet_click(index){
					this.addtype = index?true:false;
					this.modal_show = true;
					
				},
				sheet_open(){
					this.addname = "";
					this.sheet_show = true;
					
					// if(index)
					// 	this.list.push(1);
					// else
					// 	this.name  =  "13243";
				},
				swipe_open(index){
					this.list[index].show = true;
					this.list.map((val, idx) => {
						if(index != idx) this.list[idx].show = false;
					})
				},
				swipe_click(index,index1){
					var name:string = this.list[index].data;
					this.list.splice(index, 1);
					this.$u.toast(`删除了${name}`);
					uni.request({
						url: 'http://www.cloudcd.top:8080/getfile',
						data: {
							request:"del",
							user:main.data.user,
						    src: main.data.src,
							name:name
						},
						header: {
							'custom-type': 'application/json'
						},
						success: (res) => {
							console.log(res.data);
						}
					})
				},
				// 如果打开一个的时候，不需要关闭其他，则无需实现本方法
				open(index) {
					// 先将正在被操作的swipeAction标记为打开状态，否则由于props的特性限制，
					// 原本为'false'，再次设置为'false'会无效
					this.list[index].show = true;
					this.list.map((val, idx) => {
						if(index != idx) this.list[idx].show = false;
					})
				}
			}
		};
