






function getsrc(data:string):string{
	var src:string = "/";
	for(var i:number=0;i<data.length;i+=4)
		src+=(data.substring(i,i+4)+"/");
	return src;
}
export  class User{
	public username:string;//5~10位
	public password:string;//5~10位
	public src:string;
	constructor(){
		this.clear();
	}
	public clear():void{
		this.src = "";
		this.password = "";
		this.username = "";
	}
	public check(data:string):string{
		var i:number = 1;
		if(/^[a-zA-Z_][0-9a-zA-Z_]{5,14}$/.test(data))
			return false;
		return "请输入以字母或者下划线开头 6~15位由数字或者字母组成的字符串"
	}
	public login():boolean{
		this.src = getsrc(this.username);
		
		uni.request({
			url: 'http://www.cloudcd.top:8080/login',
			data: {
			    user:this
			},
			method: 'POST',
			header: {
				'custom-type': 'application/json'
			},
			success: (res) => {
				this.src = "/home/ide/";
				console.log(res.data);
				return true;
			}
		})
	}
}
